p, div {
  margin: 0;
  padding: 0; }

@font-face {
  font-family: 'Poiret One';
  src: url("../fonts/poiret/poiret-400-normal.woff2") format("woff2"), url("../fonts/poiret/poiret-400-normal.woff") format("woff"), url("../fonts/poiret/poiret-400-normal.ttf") format("truetype");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: 'Agavantgardecyr';
  src: url("../fonts/agavantgardecyr-book.ttf") format("truetype");
  font-weight: 400;
  font-style: normal; }

* {
  box-sizing: border-box;
  outline: none !important; }

html {
  height: 100%;
  min-width: 320px;
  overflow-x: hidden;
  font-size: 16px;
  line-height: 24px; }
  @media (max-width: 1023px) {
    html {
      font-size: 0.875rem;
      line-height: 20px; } }

body {
  font-family: "Poiret One", sans-serif;
  color: #ffffff;
  font-weight: normal;
  overflow: hidden;
  background-color: #070707;
  word-wrap: break-word; }

.b-container {
  width: 100%;
  max-width: 1480px;
  margin: 60px auto 0;
  padding: 0 40px 60px;
  box-sizing: border-box; }
  @media (max-width: 1023px) {
    .b-container {
      padding: 0 15px 30px;
      margin-top: 30px; } }

img {
  vertical-align: bottom; }

ul,
ol {
  list-style-type: none;
  margin: 0;
  padding: 0; }

input::-ms-clear {
  display: none; }

.header {
  position: fixed;
  z-index: 1000;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 38px;
  margin-top: 45px; }

.burger {
  display: flex;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;
  width: 39px; }
  .burger div {
    height: 2px;
    width: 100%;
    background-color: #ffffff;
    margin: 2.5px 0; }

.toggle-lang {
  display: flex;
  position: relative;
  align-items: center; }
  .toggle-lang__item {
    margin: 0 15px;
    font-family: 'Agavantgardecyr', sans-serif;
    font-size: 1.125rem;
    letter-spacing: 0.12rem;
    text-transform: uppercase;
    color: #484848; }
    .toggle-lang__item--active {
      color: #FFF5F5; }
  .toggle-lang:before {
    content: '';
    position: absolute;
    height: 13px;
    width: 1px;
    background-color: #949494;
    margin: 0 auto;
    right: 0;
    left: 0; }

.logo {
  font-style: normal;
  font-weight: normal;
  font-size: 1.875rem;
  line-height: 2.1875rem;
  letter-spacing: 0.53em;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none; }

.billboard {
  min-height: 100vh;
  position: relative;
  overflow: hidden; }
  .billboard__container {
    margin-top: 212px;
    position: relative;
    z-index: 5;
    pointer-events: none; }
  .billboard__title {
    font-size: 4rem;
    line-height: 4.6875rem;
    letter-spacing: 0.53em;
    text-transform: uppercase;
    display: flex;
    flex-direction: column; }
  .billboard__subtitles {
    display: flex; }
  .billboard__vertical {
    font-size: 1.5rem;
    line-height: 1.75rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 14px; }
    .billboard__vertical--d {
      top: 100%; }

.nav {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  transform: translateX(-100%);
  color: #070707;
  padding: 0 70px;
  transition: ease-out 0.3s; }
  .nav__close {
    position: absolute;
    top: 50px;
    right: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 26px;
    height: 26px;
    cursor: pointer;
    transition: ease-out 0.2s; }
    .nav__close div {
      position: absolute;
      height: 2px;
      width: 100%;
      background-color: #070707; }
      .nav__close div:first-child {
        transform: rotate(-45deg); }
      .nav__close div:last-child {
        transform: rotate(45deg); }
    .nav__close:hover {
      transform: rotate(180deg); }
  .nav__items {
    display: flex;
    flex-direction: column;
    justify-content: center; }
  .nav__item {
    font-family: 'Agavantgardecyr', sans-serif;
    color: inherit;
    text-decoration: none;
    text-align: center;
    font-size: 1.125rem;
    line-height: 1.0625rem;
    letter-spacing: 0.12em;
    text-transform: uppercase;
    margin: 25px 0;
    position: relative;
    color: #686666;
    transition: linear 0.2s; }
    .nav__item:after {
      content: '';
      position: absolute;
      margin: 0 auto;
      right: 0;
      left: 0;
      height: 2px;
      width: 43px;
      background-color: #D2D2D2;
      bottom: -24px; }
    .nav__item:last-child:after {
      display: none; }
    .nav__item:hover {
      color: #070707; }
  .nav--open {
    transform: translateX(0); }

#particles-js {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; }
